      </div>
      <router-view class="sidebar-right"></router-view>
    </div>


</template>

<script>
export default {
}
</script>

<style lang="less" scoped>
.sidebar{
  width: 1200px;
  background: #FAFBFF;
  margin: 0 auto;
    .sidebar-left{
      float: left;
      width: 200px;
      height: 1000px;
      background: #fff;
      ul{
        height: 100%;
        border: 1px solid #e8e6f3;
        li{
          height: 50px;
          border-bottom: 1px solid #e8e6f3;
          text-align: center;
          line-height: 50px;
