          font-size: 16px;
        }
        .act{
          color: #2e53ac;
          background: #f5f6fb;
          font-weight: 700;
        }
      }
    }
    .sidebar-right{
      width: 990px;
      height: 1000px;
      
      float: right;
    }
}
    .waybill-header{
        height: 90px;
        width:800px;
        display: flex;
        justify-content: space-between;
        line-height: 90px;
        /*border:1px solid #ccc;*/
        
    }
    .waybill-header .header-zuo{
            display: flex;
            
        }
        .waybill-header .header-zuo h2{
                color: #4046b6;
                margin-right: 30px;
            }
        .waybill-header p{
            color: #ccc;
            font-size: 13px;
        }

</style>



